import React,{Component} from 'react';
import Input from './Input.js' 
import Card from './Card.js'
import './index.css'
import Searchfield from './Searchfield.js'
import Quest from './Quest'
class App extends Component {
	constructor() {
		super()
		this.state = {
			inputbot:'',
	        userbot:'',
	        route:'home'
	    }
	}

	onInputChange = (event) => {
		this.setState({input:event.target.value})
	}

	

    onButtonChange = (event) =>{
    	this.setState({userbot:this.state.input})
    }
    
    onCardClick =() =>{
        this.setState({route:'Quest'})
     }

	render() {

		return (

		<div>
		{ !(this.state.userbot) 
			? <Input onChange={this.onInputChange} onSubmit={this.onButtonChange}/>
		    : this.state.route === 'Quest' 
		    ? <Quest/>
		    : <div>
		    <Searchfield onChange={this.onInputChange} onSubmit={this.onButtonChange} onSearch={this.onSearchChange}/>
		    <Card newrobo={this.state.userbot} cardClick={this.onCardClick}/>
		  </div>
		  
		 }  
		</div>
	
		)
}
}

export default App;

