import React from 'react'
import './index.css';

const Searchfield = ({onChange,onSubmit}) => {
	return (
<div className="tc br3 pb2 bgsearch ">
        <input onChange={onChange}  className="pa3 mt2 br3 outline-0" type="array" placeholder="Enter your name here" min="3"/>
		<input onClick={onSubmit}   className="br3 mt2 pa3 ml2 bg-white outline-0" type="submit"/>
</div>
		)
}

export default Searchfield;

