import React from 'react'
import './logo.png'

const Input = ({onChange,onSubmit}) => {
				return (
		<div className="tc mt4">
		
		<span className="wt7 ma4 br3 pa3 mt1  dib grow shadow-5 "> 
		<img className="br3" alt="Robot" src={require('./logo.png')}/>
		<h1>"The end of the world is upon us
		 and you are handpicked to face the dark
		  forces that have joined hands to destroy planet Earth.
		  Enter your name below to find out who is on your team 
		  and which dark forces you are about to face"
		 </h1>
		 </span>

		 <nav>
		 
		<input onChange={onChange}  className="pa3 br3 outline-0 " placeholder="Enter your name here" min="3" />
		<input onClick={onSubmit}   className="br3 ml2 pa3 bg-white outline-0" type="submit"/>
		</nav>
		</div>
		)
			}

export default Input;


